import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LoadingService, markAsDirtyDeep, MessageService } from 'merc-lib';
import { loginAnimation } from 'src/app/core/animations/login.animation';
import { COMMON } from 'src/app/shared/consts/global.const';
import { ROUTING_PATH } from 'src/app/shared/consts/routing-path.const';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  loginType = LoginType;

  validateForm = this.fb.group({});

  currentType = LoginType.login;

  // TODO: hardcode 帳密
  account = {
    email: 'ipservice@impacttaiwan.org',
    userWord: 'impact'
  };

  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private loadingService: LoadingService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.initForm();
  }

  private initForm(): void {
    this.validateForm.addControl('email', new FormControl('', [Validators.required]));
    this.validateForm.addControl('userWord', new FormControl('', [Validators.required]));
  }

  onArrowClick(): void {
    const loginPos = document.getElementById('login')?.offsetTop;
    window.scrollTo({ top: loginPos, behavior: 'smooth' });
  }

  onTypeClick(type: LoginType): void {
    this.currentType = type;

    if (this.currentType === LoginType.register) {
      this.validateForm.addControl('name', new FormControl('', [Validators.required]));
    } else {
      this.validateForm.removeControl('name');
    }
  }

  onLoginClick(): void {
    markAsDirtyDeep(this.validateForm);
    if (this.validateForm.invalid) { return; }

    // 驗證帳密
    if (this.validateForm.get('email')?.value !== this.account.email ||
      this.validateForm.get('userWord')?.value !== this.account.userWord) {
      this.messageService.errorModal('電子信箱或密碼輸入錯誤！');
      return;
    }

    // TODO: 假讀取
    this.loadingService.loading(true);
    sessionStorage.setItem(COMMON.TOKEN, 'fakeToken');
    setTimeout(() => {
      this.router.navigate([ROUTING_PATH.MAIN_PAGE]);
      this.loadingService.loading(false);
    }, 1000);
  }
}

enum LoginType {
  login = 'login',
  register = 'register'
}
