import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './core/guards/auth.guard';
import { LoginComponent } from './impact-patent/login/login.component';
import { ROUTING_PATH } from './shared/consts/routing-path.const';

const routes: Routes = [
  {
    path: '',
    redirectTo: ROUTING_PATH.MAIN_PAGE,
    pathMatch: 'full'
  },
  {
    path: ROUTING_PATH.MAIN_PAGE,
    loadChildren: () => import('./impact-patent/main-page/main-page.module').then(m => m.MainPageModule ),
    canActivate: [AuthGuard]
  },
  {
    path: ROUTING_PATH.LOGIN,
    component: LoginComponent
  },
  {
    path: '**',
    redirectTo: ROUTING_PATH.MAIN_PAGE,
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
